:root {
  --contacts-border-color: #e1e1e1;
  --contacts-color-text-time: #ababab;
  --contacts-bg-card-color: #ffffff;
}
.contacts {
  background-color: var(--green-bg-color);
  padding-bottom: 40px;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.contacts__item {
  background-color: var(--white-content-color);
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
}

.contacts__text {
  display: inline-block;
  background-color: var(--yellow-content-color);
  border-radius: 4px;
  padding-left: 24px;
  padding-right: 24px;
  font-weight: 500;
  @include font-contacts(12px, 22px);
  letter-spacing: 0.04em;
  color: var(--black-content-color);
  margin-bottom: 20px;
}

.address {
  margin-bottom: 48px;
}

.address__text {
  font-style: normal;
  font-weight: 500;
  @include font-contacts(12px, 22px);
  letter-spacing: 0.04em;
  color: var(--black-content-color);
  margin-bottom: 8px;
}

.address__link {
  position: relative;
  font-style: normal;
  font-weight: 500;
  @include font-contacts(12px, 22px);
  letter-spacing: 0.04em;
  color: var(--orange-products-bg-color);
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: border-color 400ms var(--timing-function);

  &:hover,
  &:focus {
    text-decoration: none;
    color: var(--orange-products-bg-color);
    border-color: var(--orange-products-bg-color);
  }
}

.work-list {
  margin-bottom: 48px;
  position: relative;
}

.work-list__item:not(:last-child) {
  margin-bottom: 8px;
}

.work-list__day {
  font-weight: 500;
  @include font-contacts(12px, 22px);
  color: var(--black-content-color);

  &::after {
    content: '';
    position: absolute;
    width: 100%;
    border: 1px solid var(--contacts-border-color);
    background-color: var(--contacts-border-color);
    left: 0;
    top: -24px;
  }
}

.work-list__time {
  font-weight: 500;
  @include font-contacts(12px, 22px);
  color: var(--contacts-color-text-time);

  &::after {
    content: '';
    position: absolute;
    width: 100%;
    border: 1px solid var(--contacts-border-color);
    background-color: var(--contacts-border-color);
    left: 0;
    bottom: -24px;
  }
}

.contacts-page__info {
  font-weight: 500;
  @include font-contacts(12px, 18px);
  color: var(--black-content-color);
  text-decoration: none;
}

.contacts-btn-list__button-locations {
  width: 196px;
  height: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--yellow-content-color);
  border-radius: 10px;
  border: none;
  cursor: pointer;

  font-weight: 600;
  @include font-contacts(14px, 18px);
  color: var(--black-content-color);
  outline: none;
  transition: background-color 400ms var(--timing-function),
    color 400ms var(--timing-function);

  &:hover,
  &:focus {
    background-color: #0a8817;
  }

  &:focus {
    color: var(--white-content-color);
  }
}

.contacts-btn-list__button-franchise {
  width: 196px;
  height: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: 2px solid var(--yellow-content-color);
  border-radius: 10px;
  cursor: pointer;

  font-weight: 600;
  @include font-contacts(14px, 18px);
  color: var(--black-content-color);
  outline: none;
  transition: border 400ms var(--timing-function),
    color 400ms var(--timing-function);
  &:hover,
  &:focus {
    border: 2px solid #0a8817;
  }

  &:focus {
    color: var(--white-content-color);
  }
}

.contacts-btn-list__icon {
  margin-left: 6px;
  height: 12;
  width: 12;
  stroke: var(--black-content-color);
  transition: stroke 400ms var(--timing-function);
  .contacts-btn-list__button-locations:focus &,
  .contacts-btn-list__button-franchise:focus & {
    stroke: var(--white-content-color);
  }
}

@media screen and (max-width: 767px) {
  .contacts__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-bottom: 32px;
  }

  .contacts__item {
    padding: 40px 32px;
  }

  .contacts-btn-list {
    display: flex;
    flex-direction: column;
    gap: 16px;
    text-align: center;
  }

  .contacts-btn-list__button-locations,
  .contacts-btn-list__button-franchise {
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .contacts__list {
    display: flex;
    gap: 16px;
    flex-wrap: wrap;
    margin-bottom: 60px;
  }

  .contacts__item {
    width: 224px;
    padding: 40px 24px;
    flex-basis: calc((100% - 32px) / 3);
  }

  .contacts__text {
    padding-left: 13px;
    padding-right: 13px;
    font-weight: 500;
    @include font-contacts(16px, 30px);
    margin-bottom: 32px;
  }

  .address {
    margin-bottom: 64px;
  }

  .address__text {
    @include font-contacts(14px, 26px);
    margin-bottom: 12px;
  }

  .address__link {
    @include font-contacts(14px, 26px);
  }

  .work-list {
    margin-bottom: 64px;
  }

  .work-list__item:not(:last-child) {
    margin-bottom: 12px;
  }

  .work-list__day {
    @include font-contacts(14px, 26px);
    &::after {
      top: -32px;
    }
  }

  .work-list__time {
    @include font-contacts(14px, 26px);

    &::after {
      bottom: -32px;
    }
  }

  .contacts-page__info {
    @include font-contacts(14px, 24px);
  }

  .contacts-btn-list {
    display: flex;
    justify-content: center;
    gap: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .contacts {
    padding-bottom: 120px;
  }

  .contacts__list {
    gap: 32px;
    margin-bottom: 64px;
  }

  .contacts__item {
    width: 368px;
    padding: 40px 40px;
    background-color: var(--contacts-bg-card-color);
    flex-basis: calc((100% - 64px) / 3);
  }

  .address__text {
    @include font-contacts(16px, 30px);
    margin-bottom: 8px;
  }

  .address__link {
    @include font-contacts(16px, 30px);
  }

  .work-list__day {
    @include font-contacts(16px, 30px);
  }

  .work-list__time {
    @include font-contacts(16px, 30px);
  }

  .contacts-page__info {
    @include font-contacts(16px, 30px);
  }
}
