//  ======================= mom animation  =======================
.hero-ice-mom {
  position: absolute;
  z-index: 1250;
  animation-name: mom-animation;
  @media screen and (min-width: 280px) and (max-width: 767px) {
    top: 320px;
    right: 78px;
    animation-duration: 1000ms;
  }
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    top: 140px;
    right: 90px;
    animation-duration: 1150ms;
  }
  @media screen and (min-width: 1200px) {
    top: 225px;
    right: 90px;
    animation-duration: 4150ms;
  }
}
@media screen and (min-width: 280px) and (max-width: 767px) {
  @keyframes mom-animation {
    0% {
      top: 0px;
      right: 78px;
    }
    90% {
      top: 350px;
      right: 78px;
    }
    100% {
      top: 320px;
      right: 78px;
    }
  }
}
@media screen and (min-width: 768px) and (max-width: 1199px) {
  @keyframes mom-animation {
    0% {
      top: 140px;
      right: 450px;
    }

    100% {
      top: 140px;
      right: 90px;
    }
  }
}
@media screen and (min-width: 1200px) {
  @keyframes mom-animation {
    0% {
      top: 0px;
      right: 590px;
    }

    50% {
      top: 360px;
      right: 290px;
    }

    100% {
      top: 225px;
      right: 90px;
    }
  }
}

//  ======================= /mom animation  =======================
