//  ======================= dad animation  =======================
.hero-ice-dad {
  position: absolute;
  z-index: 2400;
  animation-name: dad-animation;
  @media screen and (min-width: 280px) and (max-width: 767px) {
    top: 250px;
    right: 88px;
    animation-duration: 900ms;
  }
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    top: 30px;
    left: 530px;
    animation-duration: 900ms;
  }
  @media screen and (min-width: 1200px) {
    bottom: 370px;
    right: 100px;
    animation-duration: 3750ms;
  }
}
@media screen and (min-width: 280px) and (max-width: 767px) {
  @keyframes dad-animation {
    0% {
      top: 0px;
      right: 88px;
    }
    90% {
      top: 270px;
      right: 88px;
    }
    100% {
      top: 250px;
      right: 88px;
    }
  }
}
@media screen and (min-width: 768px) and (max-width: 1199px) {
  @keyframes dad-animation {
    0% {
      top: 30px;
      left: 200px;
    }

    100% {
      top: 30px;
      left: 530px;
    }
  }
}
@media screen and (min-width: 1200px) {
  @keyframes dad-animation {
    0% {
      bottom: 0px;
      right: 1050px;
    }
    33% {
      bottom: 370px;
      right: 650px;
    }
    66% {
      bottom: 0px;
      right: 550px;
    }

    100% {
      bottom: 370px;
      right: 100px;
    }
  }
}

