.advantages__list {
    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: center;
        gap: 80px;
        // flex-basis: calc((100% - 312px) / 3);
    }

    @media screen and (min-width: 1200px) {
        justify-content: space-between;
        gap: 0px;
        // flex-basis: calc((100% - 96px) / 3);
    }
}

.advantages__item {
    &::before {
        content: "";
        display: block;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 16px;
        width: 50px;
        height: 50px;
        background-repeat: no-repeat;
        background-color: var(--yellow-content-color);
        border-radius: 50%;
        background-position: 50% 50%;

        @media screen and (min-width: 768px) {
            margin-bottom: 32px;
        }

        @media screen and (min-width: 1200px) {
            margin-bottom: 40px;
        }
    }

    @media screen and (min-width: 768px) {
        flex-basis: calc((100% - 312px) / 3);
    }

    @media screen and (min-width: 1200px) {
        flex-basis: calc((100% - 96px) / 3);
    }

    &:not(:last-child) {
        margin-bottom: 22px;

        @media screen and (min-width: 768px) {
            margin-bottom: 0px;
        }
    }
}

.advantages__item--cow {
    &::before {
        background-image: url("../images/advantages-cow.png");
    }
}

.advantages__item--weight {
    &::before {
        background-image: url("../images/advantages-weight.png");
    }
}

.advantages__item--ice-cream {
    &::before {
        background-image: url("../images/advantages-ice-cream.png");
    }
}

.advantages__value {
    font-family: "roboto";
    @include font(36px, 700, 42px);

    color: var(--white-content-color);
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 18px;

    @media screen and (min-width: 1200px) {
        font-family: "Montserrat";
        @include font(58px, 700, 71px);
        margin-bottom: 16px;
        text-transform: lowercase;
    }
}

.advantages__description {
    display: block;
    margin-left: auto;
    margin-right: auto;
    @include font(12px, 700, 20px);
    color: var(--white-content-color);
    text-align: center;
    width: 230px;

    @media screen and (min-width: 768px) {
        width: 155px;
    }

    @media screen and (min-width: 1200px) {
        width: 368px;

        @include font(16px, 400, 28px, 0.02em);
    }
}
